
import ymask from '../components/Mask'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'apply',
data() {
return {
maskShow: false,
canRedeem: 0,
amount: '',
successInfo: {
fundName: '',
amount: 0,
verifyDate: ''
},
buyBackground: 'buy2',
btnMsg: "赎回"
}
},
computed: {

},
components: {
XHeader,
ymask
},
mounted() {
this.redeemInfo()
},
methods: {
redeemInfo(fundCode) { //基金可赎回份额查询
let _this = this;
this.post({
url: "/fundTrans/queryCanRedeem/v1.0",
data: {
fundCode: _this.$route.query.code
},
success: function(e) {
if (e.code == "0000") {
_this.canRedeem = e.result.canRedeem;
}
}
})
},
redeem() {
let _this = this;
if (this.amount == 0 || this.amount == '' || this.amount > this.canRedeem) {
return;
}
this.post({
url: "/fundTrans/redeem/v1.0",
data: {
fundCode: _this.$route.query.code,
amount: _this.amount
},
success: function(e) {
if (e.code == "0000") {
_this.successInfo = e.result;
_this.maskShow = true;
_this.amount = '';
_this.canRedeem = _this.canRedeem - e.result.amount;
} else if (e.code == "1002" || e.code == "1005" || e.code == "1006" || e.code == "1007" || e.code == "1008") {
_this.$vux.alert.show({
title: '提示',
content: e.msg
})
}
}
})
},
clearNoNum() {
let obj=this.amount;
if(obj.replace){
obj = obj.replace(/[^\d.]/g, "");  //清除“数字”和“.”以外的字符
obj = obj.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
obj = obj.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
obj = obj.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3');//只能输入两个小数
if (obj.indexOf(".") < 0 && obj != "") {//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
obj = parseFloat(obj);
}
}
this.amount=obj;
}
}, watch: {
amount: function(n, o) {
this.clearNoNum();

if (this.amount == 0 || this.amount == '' || this.amount > this.canRedeem) {
this.buyBackground = 'buy2'
} else {
this.buyBackground = 'buy1'
}
if (this.amount > this.canRedeem) {
this.btnMsg = '赎回份额大于当前可赎回份额'
} else {
this.btnMsg = '赎回'
}
}
}
}



